import React from 'react';
import {Table} from 'react-bootstrap';
import { connect } from 'react-redux';

function Cart(props){
    return (
      <div>
          {
            props.alert닫기 === true
            ?  <div className=".alert" style={{background:"#eee", padding:"20px", margin:"20px"}}>지금 구매하시면 신규회원20% <br/>
                <button onClick={()=>{ props.dispatch({ type : 'alert닫기'}) }}>닫기</button>
                </div>
            : null  
        }
        <Table responsive>
          <tr>
            <th>#</th>
            <th>상품명</th>
            <th>수량</th>
            <th>변경</th>
          </tr>
          { props.state.map((a,i)=>{
            return (
            <tr key={i}>
              <td>{a.id}</td>
              <td>{a.name}</td>
              <td>{a.quan}</td>
              <td><button onClick={()=>{ props.dispatch({ type : '수량증가' }) }}> + </button>
                  <button onClick={()=>{ props.dispatch({ type : '수량감소' }) }}> - </button></td>
            </tr>
            )
          })  }
        </Table>
      </div>
    )
  }


function 함수명(state){
 return{
     state : state.reducer,
     alert닫기 : state.reducer2
 }
}

export default connect(함수명)(Cart)
